import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';


import {AppComponent} from './app.component';
import {AppRoutingModule} from './app-routing.module';

import {NavComponent} from './nav/nav.component';
import {SignInComponent} from './sign-in/sign-in.component';
import {SignUpComponent} from './sign-up/sign-up.component';
import {CalendarComponent} from './calendar/calendar.component';
import {ClientsComponent} from './clients/clients.component';
import {ServicesComponent} from './services/services.component';
import {AvailabilityComponent} from './availability/availability.component';
import {BlackOutTimesComponent} from './black-out-times/black-out-times.component';
import {ProfileComponent} from './profile/profile.component';
import {HttpClientModule} from '@angular/common/http';
import {SignUpService} from './sign-up/sign-up.service';
import { ClientsAddComponent } from './clients/clients-add/clients-add.component';
import { ProfileEditComponent } from './profile/profile-edit/profile-edit.component';
import { AppointmentComponent } from './calendar/appointment/appointment.component';
import { ClientDetailsComponent } from './clients/client-details/client-details.component';
import { AddServiceComponent } from './services/add-service/add-service.component';
import { EditServiceComponent } from './services/edit-service/edit-service.component';
import { ThankSignUpComponent } from './sign-up/thank-sign-up/thank-sign-up.component';
import {SignInService} from './sign-in/sign-in.service';
import {ProfileService} from './profile/profile.service';
import {CalendarService} from './calendar/calendar.service';


import { HashLocationStrategy, LocationStrategy } from '@angular/common';


@NgModule({
    declarations: [
        AppComponent,
        NavComponent,
        SignInComponent,
        SignUpComponent,
        CalendarComponent,
        ClientsComponent,
        ServicesComponent,
        AvailabilityComponent,
        BlackOutTimesComponent,
        ProfileComponent,
        ClientsAddComponent,
        ProfileEditComponent,
        AppointmentComponent,
        ClientDetailsComponent,
        AddServiceComponent,
        EditServiceComponent,
        ThankSignUpComponent
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
        HttpClientModule
    ],
    providers: [
        SignUpService,
        SignInService,
        ProfileService,
        CalendarService,
        {provide: LocationStrategy, useClass: HashLocationStrategy}
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}



