import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';


const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class SignInService {
    constructor(private http: HttpClient) {
    }

    signInUrl = 'https://dgdq9nx784.execute-api.us-east-1.amazonaws.com/test/users/signin';

    putSignInData(signInData): Observable<any> {
        return this.http.post(this.signInUrl, signInData , httpOptions);
    }
}





