import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';


const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};


@Injectable()
export class CalendarService {

  constructor(private http: HttpClient) { }

    getCalendarUrl = 'https://dgdq9nx784.execute-api.us-east-1.amazonaws.com/test/users/13/schedule?period=month&date=2018-03-24';

    getCalendardata(): Observable<any> {
      return this.http.get(this.getCalendarUrl, httpOptions);
    }
}



