import {Component, OnInit} from '@angular/core';
import {SignInService} from './sign-in.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-sign-in',
    templateUrl: './sign-in.component.html',
    styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit {

    constructor(private signInService: SignInService, private router: Router) {
    }

    ngOnInit() {
    }

    /* Send user data for sign-up
  ------------------------------------*/
    public signInUser(e) {
        e.preventDefault();
        const userPhone: number = e.target.elements[1].value,
            userPassword: string = e.target.elements[2].value;
        const usersData: any = {
            'phone': userPhone,
            'password': userPassword
        };
        this.signInService.putSignInData(usersData)
            .subscribe(
                (data: any) => {
                    localStorage.setItem('stylistPlanerCurrentUserId', data.result);
                    console.log(data);
                    console.log(localStorage);

                    this.router.navigate(['/service']);
                },
                error => {
                    console.log('error');
                });
        console.log(userPhone);
        console.log(userPassword);
        return false;
    }

}



